/* ===========================
   1. VARIABLES & RESET
   =========================== */
:root {
    /* Modern Professional Palette */
    --primary-gradient: linear-gradient(135deg, #6366f1 0%, #4f46e5 100%); /* Indigo to Violet */
    --primary-color: #4f46e5;
    --secondary-color: #64748b;     /* Slate Grey */
    --accent-color: #0ea5e9;        /* Sky Blue */
    
    --dark-bg: #0f172a;             /* Dark Slate */
    --light-bg: #f8fafc;            /* Very Light Grey */
    --card-bg: #ffffff;
    
    --text-dark: #1e293b;
    --text-light: #ffffff;
    
    --nav-height: 80px;
    --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    --shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);
    --shadow-md: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
    --shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Inter', 'Segoe UI', sans-serif; /* Modern Font Stack */
    line-height: 1.7;
    background-color: var(--light-bg);
    color: var(--text-dark);
    -webkit-font-smoothing: antialiased;
}

/* Utilities */
.page-section {
    padding: 6rem 2rem;
    max-width: 1280px;
    margin: 0 auto;
    min-height: 80vh;
}

.section-title {
    text-align: center;
    font-size: 2.5rem;
    font-weight: 800;
    margin-bottom: 4rem;
    color: var(--dark-bg);
    position: relative;
    display: inline-block;
    left: 50%;
    transform: translateX(-50%);
}

.section-title::after {
    content: '';
    display: block;
    width: 60px;
    height: 4px;
    background: var(--primary-gradient);
    margin: 10px auto 0;
    border-radius: 2px;
}

/* Modern Buttons */
.btn {
    padding: 14px 28px;
    border-radius: 8px;
    text-decoration: none;
    font-weight: 600;
    cursor: pointer;
    transition: var(--transition);
    display: inline-block;
    border: 1px solid transparent;
    letter-spacing: 0.5px;
}

.primary {
    background: var(--primary-gradient);
    color: var(--text-light);
    box-shadow: 0 4px 12px rgba(79, 70, 229, 0.3);
}

.primary:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(79, 70, 229, 0.4);
}

.secondary {
    background: white;
    border: 1px solid #e2e8f0;
    color: var(--text-dark);
    margin-left: 15px;
}

.secondary:hover {
    border-color: var(--primary-color);
    color: var(--primary-color);
    background: #f8fafc;
}

/* ===========================
   2. HOME PAGE (HERO)
   =========================== */
.home-container {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    position: relative;
    /* Modern Dark overlay with professional image */
    background: linear-gradient(rgba(15, 23, 42, 0.85), rgba(15, 23, 42, 0.9)), 
                url('https://images.unsplash.com/photo-1498050108023-c5249f4df085?auto=format&fit=crop&w=1920&q=80') no-repeat center/cover;
    background-attachment: fixed; /* Parallax effect */
    color: var(--text-light);
    margin-top: calc(-1 * var(--nav-height));
    padding: 0 1rem;
}

.hero-content {
    max-width: 800px;
    z-index: 2;
}

.hero-content h1 {
    font-size: 4rem;
    font-weight: 800;
    margin-bottom: 1.5rem;
    line-height: 1.1;
    letter-spacing: -1px;
}

.highlight {
    color: var(--primary-color); /* Fallback for older browsers */
    background: var(--primary-gradient);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    display: inline-block;
}

.hero-content h2 {
    font-size: 1.5rem;
    font-weight: 400;
    margin-bottom: 2.5rem;
    color: #cbd5e1;
}

/* ===========================
   3. ABOUT PAGE
   =========================== */
.about-content {
    background: var(--card-bg);
    padding: 3rem;
    border-radius: 16px;
    box-shadow: var(--shadow-lg);
    border: 1px solid #e2e8f0;
}

.stats {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 2rem;
    margin-top: 3rem;
    padding-top: 3rem;
    border-top: 1px solid #f1f5f9;
}

.stat-item strong {
    display: block;
    font-size: 2.5rem;
    font-weight: 800;
    color: var(--primary-color);
    line-height: 1;
    margin-bottom: 0.5rem;
}

/* ===========================
   4. PROJECTS PAGE
   =========================== */
.projects-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 2.5rem;
}

.project-card {
    background: var(--card-bg);
    border-radius: 12px;
    overflow: hidden;
    box-shadow: var(--shadow-md);
    transition: var(--transition);
    border: 1px solid #f1f5f9;
    display: flex;
    flex-direction: column;
}

.project-card:hover {
    transform: translateY(-8px);
    box-shadow: var(--shadow-lg);
}

.card-image {
    height: 220px;
    width: 100%;
    background-color: #cbd5e1;
    background-position: center;
    background-size: cover;
    transition: transform 0.5s ease;
}

.project-card:hover .card-image {
    transform: scale(1.03);
}

.card-content {
    padding: 1.5rem;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.card-content h3 {
    font-size: 1.25rem;
    margin-bottom: 0.5rem;
    color: var(--dark-bg);
}

.tech-stack {
    display: inline-block;
    margin: 1rem 0;
    font-size: 0.85rem;
    color: var(--primary-color);
    background: #eef2ff;
    padding: 4px 12px;
    border-radius: 20px;
    font-weight: 600;
}

.card-links {
    margin-top: auto;
    padding-top: 1rem;
    border-top: 1px solid #f1f5f9;
}

.card-links a {
    margin-right: 20px;
    color: var(--text-dark);
    text-decoration: none;
    font-weight: 600;
    font-size: 0.9rem;
    transition: color 0.2s;
}

.card-links a:hover {
    color: var(--primary-color);
}

/* ===========================
   5. SKILLS PAGE
   =========================== */
.skills-container {
    max-width: 800px;
    margin: 0 auto;
    background: var(--card-bg);
    padding: 3rem;
    border-radius: 16px;
    box-shadow: var(--shadow-lg);
}

.skill-bar {
    margin-bottom: 2rem;
}

.skill-info {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    font-weight: 600;
    color: var(--dark-bg);
}

.progress-bg {
    height: 10px;
    background-color: #f1f5f9;
    border-radius: 10px;
    overflow: hidden;
}

.progress-fill {
    height: 100%;
    background: var(--primary-gradient);
    border-radius: 10px;
    position: relative;
}

/* ===========================
   6. CONTACT PAGE
   =========================== */
.contact-form {
    max-width: 700px;
    margin: 0 auto;
    background: var(--card-bg);
    padding: 3rem;
    border-radius: 16px;
    box-shadow: var(--shadow-lg);
}

.form-group label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: 600;
    color: var(--secondary-color);
}

.form-group input,
.form-group textarea {
    width: 100%;
    padding: 16px;
    border: 2px solid #e2e8f0;
    border-radius: 8px;
    font-size: 1rem;
    transition: border-color 0.2s;
    background: #f8fafc;
}

.form-group input:focus,
.form-group textarea:focus {
    outline: none;
    border-color: var(--primary-color);
    background: white;
}

/* ===========================
   RESUME PAGE (Updated for Patel Kishan)
   =========================== */
.resume-section {
    --res-primary: #1e293b;    /* Dark Slate */
    --res-accent: #4f46e5;     /* Indigo */
    --res-text: #475569;       /* Soft Grey */
    --res-line: #cbd5e1;       /* Light Grey */
    
    background: white;
    max-width: 900px;
    margin: 4rem auto;
    padding: 4rem;
    box-shadow: 0 10px 30px -10px rgba(0, 0, 0, 0.15);
    border-radius: 8px; 
    font-family: 'Inter', 'Segoe UI', sans-serif;
    color: var(--res-text);
    line-height: 1;
}

/* --- Header Section --- */
.resume-header {
    border-bottom: 3px solid var(--res-accent);
    padding-bottom: 1.5rem;
    margin-bottom: 2.5rem;
}

.resume-header h3 {
    font-size: 2.8rem;
    color: var(--res-primary);
    margin: 0;
    line-height: 1.1;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.resume-header p {
    font-size: 1.2rem;
    color: var(--res-accent);
    font-weight: 700;
    margin: 5px 0 10px 0;
    text-transform: uppercase;
}

/* Contact Info Bar */
.contact-info {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    font-size: 0.95rem;
    color: var(--res-primary);
    font-weight: 500;
    margin-bottom: 15px;
}

.contact-info .links a {
    color: var(--res-accent);
    text-decoration: none;
    font-weight: 600;
}

.summary small {
    display: block;
    font-size: 1rem;
    line-height: 1.6;
    color: var(--res-text);
}

/* --- Section Blocks --- */
.resume-block {
    margin-bottom: 2.5rem;
}

.resume-block h4 {
    color: var(--res-primary);
    font-size: 1.3rem;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    border-bottom: 1px solid var(--res-line);
    padding-bottom: 10px;
    margin-bottom: 1.5rem;
    font-weight: 800;
    display: flex;
    align-items: center;
}

.resume-block h4::before {
    content: '';
    display: inline-block;
    width: 12px;
    height: 12px;
    background-color: var(--res-accent);
    margin-right: 12px;
    border-radius: 2px;
}

/* --- Items --- */
.job, .school {
    margin-bottom: 1.5rem;
    padding-left: 20px;
    border-left: 2px solid transparent;
    transition: all 0.2s;
}

.job:hover, .school:hover {
    border-left: 2px solid var(--res-accent);
}

.job h5, .school h5 {
    font-size: 1.15rem;
    color: var(--res-primary);
    margin: 0 0 4px 0;
    font-weight: 700;
}

.job small, .school small {
    font-size: 0.95rem;
    color: var(--res-accent);
    font-weight: 600;
    display: block;
    margin-bottom: 8px;
}

.job p, .school p {
    margin: 0;
}

/* --- Bullet Lists (Projects & Certificates) --- */
.job ul, .school ul {
    list-style-type: disc;
    padding-left: 20px;
    margin: 5px 0 0 0;
}

.job ul li, .school ul li {
    margin-bottom: 5px;
    font-size: 0.95rem;
    line-height: 1.5;
}

/* --- Print Styles --- */
@media print {
    /* 1. Hide unwanted elements */
    nav, 
    .navbar, 
    .btn, 
    .section-title, 
    .menu-toggle { 
        display: none !important; 
    }

    /* 2. Reset Page Layout */
    body, 
    html, 
    #root { 
        background: white !important; 
        margin: 0 !important; 
        padding: 0 !important; 
    }

    /* 3. Resume Container - Maximize Width */
    .resume-section { 
        box-shadow: none !important; 
        margin: 0 !important; 
        width: 100% !important; 
        max-width: 100% !important; 
        padding: 0 !important; 
    }

    /* 4. Color Adjustments for Printing */
    .resume-header h3 { 
        color: #1e293b !important; 
    }

    .resume-header p, 
    .contact-info { 
        color: #4f46e5 !important; 
        -webkit-print-color-adjust: exact; 
        print-color-adjust: exact;
    }
}