.navbar {
    height: var(--nav-height);
    /* Translucent dark background */
    background: rgba(15, 23, 42, 0.85);
    /* Frosted Glass Blur */
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
    
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 5%;
    position: sticky;
    top: 0;
    z-index: 1000;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.logo {
    color: var(--text-light);
    font-size: 1.5rem;
    font-weight: 800;
    letter-spacing: -0.5px;
    text-decoration: none;
}

.logo span {
    color: #818cf8; /* Soft Indigo */
}

.nav-links {
    display: flex;
    list-style: none;
    align-items: center;
    gap: 2rem;
}

.nav-links a {
    color: #cbd5e1;
    text-decoration: none;
    font-size: 0.95rem;
    font-weight: 500;
    transition: color 0.3s;
    position: relative;
}

.nav-links a:hover {
    color: white;
}

/* Hover Dot Animation */
.nav-links a::after {
    content: '';
    position: absolute;
    width: 0;
    height: 2px;
    bottom: -4px;
    left: 0;
    background: #818cf8;
    transition: width 0.3s;
}

.nav-links a:hover::after {
    width: 100%;
}

.btn-contact {
    background: var(--primary-gradient);
    padding: 10px 24px;
    border-radius: 50px;
    color: white !important;
    font-weight: 600;
    box-shadow: 0 4px 15px rgba(79, 70, 229, 0.4);
}

.btn-contact:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(79, 70, 229, 0.6);
}

.btn-contact::after {
    display: none;
}

.menu-toggle {
    display: none;
    flex-direction: column;
    cursor: pointer;
    gap: 6px;
}

.bar {
    width: 30px;
    height: 2px;
    background-color: white;
    transition: 0.3s;
}